'use strict';

// MODULES //

var proc = require( 'process' );


// MAIN //

/**
* An array containing command-line arguments passed when launching the calling process.
*
* @name ARGV
* @type {StringArray}
*
* @example
* console.log( ARGV );
* // => [...]
*/
var ARGV = proc.argv;


// EXPORTS //

module.exports = ARGV;
