'use strict';

/**
* Change the current working directory.
*
* @module @stdlib/process/chdir
*
* @example
* var chdir = require( '@stdlib/process/chdir' );
*
* var err = chdir( __dirname );
* if ( err ) {
*     throw err;
* }
*/

// MODULES //

var chdir = require( './main.js' );


// EXPORTS //

module.exports = chdir;
