'use strict';

/**
* An array containing command-line arguments passed when launching the calling process.
*
* @module @stdlib/process/argv
*
* @example
* var ARGV = require( '@stdlib/process/argv' );
*
* console.log( ARGV );
* // => [...]
*/

// MODULES //

var ARGV = require( './main.js' );


// EXPORTS //

module.exports = ARGV;
